header {
  background-color: #5CAEE9;
  color: #fff;
  height: 50px;
}

header .header-name {
  flex: 0 1 31%;
}

header .header-name a {
  text-decoration: none;
  color: #fff;
  margin-left: 20px;
}

@media (max-width: 800px) {
  header .header-name a {
    display: none;
  }
}

header .header-sign-out {
  margin: 0 20px;
  padding-left: 20px;
  border-left: 1px solid #fff;
  cursor: pointer;
}

header .search-bar-container {
  position: relative;
}

header .search-bar-container .search-snippets {
  padding: 8px 11px;
  width: 270px;
}

header .search-bar-container .icon-search {
  position: absolute;
  right: 0;
  top: 1px;
  color: #000;
  padding: 6px 10px;
  border-left: 1px solid #E3E3E3;
  cursor: pointer;
}

.main-container {
  height: calc(100% - 50px);
}

.left-panel {
  background-color: #181818;
  flex: 0 1 15%;
  transition: all 1s;
}

.left-panel .filters, .left-panel .labels {
  height: 40px;
  border-top: 1px solid #212121;
  border-bottom: 1px solid #0a0c0e;
  color: #fff;
  cursor: pointer;
}

.left-panel .filters:hover, .left-panel .labels:hover {
  background-color: #111111;
}

@media (max-width: 1280px) {
  .left-panel .filters, .left-panel .labels {
    justify-content: center;
  }
}

.left-panel .filters .left-panel-icon, .left-panel .labels .left-panel-icon {
  margin-left: 20px;
  margin-right: 10px;
}

@media (max-width: 1280px) {
  .left-panel .filters .left-panel-icon, .left-panel .labels .left-panel-icon {
    margin-left: 0;
  }
}

.left-panel .filters span:last-of-type, .left-panel .labels span:last-of-type {
  margin-top: 2px;
}

.left-panel .filter-selected {
  background-color: #fff;
  color: #000;
}

.left-panel .filter-selected:hover {
  background-color: #fff;
  color: #000;
}

.left-panel .new-snippet-filter {
  margin-top: 20px;
  margin-bottom: 50px;
}

.left-panel .labels-filter {
  margin-top: 50px;
}

.right-panel {
  background-color: #F5F5F5;
  flex: 0 1 85%;
  max-height: calc(100vh - 50px);
  position: relative;
  overflow: hidden;
}

.right-panel md-content {
  background: #F5F5F5;
}

.right-panel .no-snippets {
  text-align: center;
  color: #181818;
}
